.customer {
  padding-bottom: 120px;
}

.customer__list {
  margin-top: 60px;
  display: flex;
  flex-wrap: wrap;
  gap: 32px;
}

.customer__item {
  width: 240px;
  height: 100px;
  border: 1px solid #131416;

  display: flex;
  align-items: center;
  justify-content: center;

  transform: scale(1);
  transition-property: border, transform;
  transition-duration: 250ms;
  transition-timing-function: (0.4, 0, 0.2, 1);

  &:hover,
  &:focus {
    transform: scale(1.25);
  }
}

.customer__icon {
  height: 16px;
  width: auto;

  fill: #131416;

  transition-property: fill;
  transition-duration: 250ms;
  transition-timing-function: (0.4, 0, 0.2, 1);
}

.icon1 {
  background-image: url(/src/images/customer-icon1.svg);
  background-repeat: no-repeat;
  background-position: center;
  height: 16px;
  width: auto;
}

